  height: 134px;
  display: flex;
  align-items: center;
  flex-direction: row;
  align-items: flex-start;
}

#index #news .news-left .news-lists .news-item-first img {
  width: 176px;
  margin-right: 16px;
}

#index #news .news-left .news-lists ul li {
  display: flex;
  align-items: center;
  flex-direction: row;
  height: 48px;
  font-size: 16px;
  font-family: NotoSansHans-DemiLight;
  font-weight: normal;
  color: #272727;
  justify-content: space-between;
  border-bottom: 1px dotted #272727;
}

#index #news .news-right {
  text-align: right;
  align-self: flex-start;
}

#index #news .news-right img + img {
  margin-top: 30px;
}
